.burger {
  position: relative;
  width: 40px;
  height: 30px;
  background: transparent;
  cursor: pointer;
  display: block;
}

.burger input {
  display: none;
}

.burger span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  background: white;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}

.burger span:nth-of-type(1) {
  top: 0px;
  transform-origin: left center;
}

.burger span:nth-of-type(2) {
  top: 50%;
  transform: translateY(-50%);
  transform-origin: left center;
}

.burger span:nth-of-type(3) {
  top: 100%;
  transform-origin: left center;
  transform: translateY(-100%);
}

.burger input:checked ~ span:nth-of-type(1) {
  transform: rotate(45deg);
  top: 0px;
  left: 5px;
}

.burger input:checked ~ span:nth-of-type(2) {
  width: 0%;
  opacity: 0;
}

.burger input:checked ~ span:nth-of-type(3) {
  transform: rotate(-45deg);
  top: 28px;
  left: 5px;
}

.tabs-products__loader {
  min-height: 510px;
  display: grid;
  place-items: center;
}

.spinner-container {
  width: 150px;
  height: 150px;
  position: relative;
  margin: 30px auto;
  overflow: hidden;
}

.spinner {
  position: absolute;
  width: calc(100% - 9.9px);
  height: calc(100% - 9.9px);
  border: 5px solid transparent;
  border-radius: 50%;
  border-top-color: #94d7e2;
  animation: spin 5s cubic-bezier(0.17, 0.49, 0.96, 0.79) infinite;
}

.loader-points {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.1921568627);
  backdrop-filter: blur(1px);
  z-index: -10;
  opacity: 0;
}
.loader-points.visible {
  z-index: 10;
  opacity: 1;
}
.loader-points .loader {
  display: flex;
  justify-content: center;
  align-items: center;
  --color: white;
  --animation: 2s ease-in-out infinite;
}
.loader-points .loader .circle {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 20px;
  height: 20px;
  border: solid 2px var(--color);
  border-radius: 50%;
  margin: 0 10px;
  background-color: transparent;
  animation: circle-keys var(--animation);
}
.loader-points .loader .circle .dot {
  position: absolute;
  transform: translate(-50%, -50%);
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: var(--color);
  animation: dot-keys var(--animation);
}
.loader-points .loader .circle .outline {
  position: absolute;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  animation: outline-keys var(--animation);
}
.loader-points .circle:nth-child(2) {
  animation-delay: 0.3s;
}
.loader-points .circle:nth-child(3) {
  animation-delay: 0.6s;
}
.loader-points .circle:nth-child(4) {
  animation-delay: 0.9s;
}
.loader-points .circle:nth-child(5) {
  animation-delay: 1.2s;
}
.loader-points .circle:nth-child(2) .dot {
  animation-delay: 0.3s;
}
.loader-points .circle:nth-child(3) .dot {
  animation-delay: 0.6s;
}
.loader-points .circle:nth-child(4) .dot {
  animation-delay: 0.9s;
}
.loader-points .circle:nth-child(5) .dot {
  animation-delay: 1.2s;
}
.loader-points .circle:nth-child(1) .outline {
  animation-delay: 0.9s;
}
.loader-points .circle:nth-child(2) .outline {
  animation-delay: 1.2s;
}
.loader-points .circle:nth-child(3) .outline {
  animation-delay: 1.5s;
}
.loader-points .circle:nth-child(4) .outline {
  animation-delay: 1.8s;
}
.loader-points .circle:nth-child(5) .outline {
  animation-delay: 2.1s;
}

