.card-product__flag--discount-percentage, .card-product__flag--new {
  height: 40px;
  width: 100px;
  position: absolute;
  z-index: 1;
  left: -36px;
  top: 0;
  padding: 0px 16px;
  display: flex;
  justify-content: center;
  align-items: end;
  font-size: 10px;
  font-weight: 600;
  background-color: #b9fab9;
  color: black;
  rotate: -48deg;
}
.card-product__flag--discount-percentage {
  background-color: #94d7e2;
}
.card-product__card {
  height: 100%;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 15px;
  overflow: hidden;
  border: 1px solid #94d7e2;
  background: #FBFBFB;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.1);
}
.card-product__card:hover img {
  scale: 1.1;
}
.card-product__card:hover .card-product__buttons {
  bottom: 10px;
  z-index: 10;
  opacity: 1;
}
.card-product__thumbnail {
  position: relative;
  height: 300px;
  width: 100%;
  overflow: hidden;
  border-radius: 10px;
}
.card-product__thumbnail img {
  height: 100%;
  object-fit: cover;
  transition: all 0.3s ease;
}
.card-product__figure {
  height: 100%;
}
.card-product__buttons {
  position: absolute;
  bottom: -60px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: baseline;
  gap: 30px;
  opacity: 0;
  transition: all 0.3s ease;
}
@media screen and (max-width: 768px) {
  .card-product__buttons {
    bottom: 10px;
    z-index: 10;
    opacity: 1;
  }
}
.card-product__tooltip {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  padding: 0.3em 0.6em;
  opacity: 0;
  pointer-events: none;
  transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
  background: #94d7e2;
  z-index: -1;
  border-radius: 8px;
  scale: 0;
  transform-origin: 0 0;
  text-transform: capitalize;
  font-weight: 400;
  font-size: 16px;
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
}
.card-product__tooltip::before {
  position: absolute;
  content: "";
  height: 0.6em;
  width: 0.6em;
  bottom: -0.2em;
  left: 50%;
  transform: translate(-50%) rotate(45deg);
  background: #94d7e2;
}
.card-product__add-cart {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.889);
  cursor: pointer;
  transition: all 0.5s ease;
}
.card-product__add-cart svg {
  fill: transparent;
}
.card-product__add-cart:hover {
  scale: 1.1;
}
.card-product__add-cart:hover .card-product__tooltip {
  top: -35%;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  scale: 1;
  font-size: 11px;
  width: max-content;
  color: black;
  animation: shake 0.5s ease-in-out both;
}
.card-product__add-cart:active {
  scale: 0.9;
}
.card-product__info {
  padding: 15px 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  text-align: center;
  font-weight: 700;
}
.card-product__h3 {
