}
.card-product__buy-free {
  position: relative;
  width: max-content;
  padding: 12px 12px 12px 42px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-weight: 600;
}
.card-product__buy-free::after {
  content: "";
  position: absolute;
  bottom: 12px;
  left: 0;
  width: 0%;
  height: 2px;
  background: white;
  transition: all 0.5s ease;
}
.card-product__buy-free i {
  width: 24px;
  height: auto;
  position: absolute;
  left: 12px;
  transition: all 0.5s ease;
