}
.card-product__buy-free svg {
  fill: transparent;
}
.card-product__buy-free:hover {
  background-color: #CCCCCC;
  padding: 12px 42px 12px 12px;
}
.card-product__buy-free:hover::after {
  width: 100%;
}
.card-product__buy-free:hover i {
  right: 12px;
  left: calc(100% - 34px);
}

